import { __ } from '@wordpress/i18n';
import {
    BaseControl
} from '@wordpress/components';
import CustomContent from '../../components/custom-content';

// Style
import './style.scss';

function ControlCustomContent( props ) {

    const {
        label,
        settingClass,
        isContent,
        hideLabel
    } = props

    const showLabel = ! hideLabel || 'undefined' === typeof hideLabel;

    return(
        <>
            <BaseControl
                className="ocean-customize-control__custom-content"
            >
                <div className={`control__custom-content-wrap ${settingClass ? settingClass : ''}`}>
                    { !! label && showLabel &&
                        <div className="control__custom-content--header">
                            <div className="control__custom-content--label">
                                <span className='customize-control-title'>{ label }</span>
                            </div>
                        </div>
                    }

                    { !! isContent && <CustomContent {...props}/> }
                </div>
            </BaseControl>
        </>
    );

}

export default ControlCustomContent;