const ControlSpacer = ( props ) => {

    const {
        top ,
        bottom
    } = props;

    const style = {
        marginTop: top ? top + 'px' : '10px',
        marginBottom: bottom ? bottom + 'px' : '10px',
    };

	return (
        <div className="ocean-customize-control__spacer" style={style}></div>
	);
};

export default ControlSpacer;