import { __ } from '@wordpress/i18n';
import {
    BaseControl
} from '@wordpress/components';
import LinkItems from '../../components/links';
import svgIcon from '../../utils/icon';

// Style
import './style.scss';

function ControlLinks( props ) {

    const {
        label,
        links,
        hideLabel,
        titleIcon,
        settingClass
    } = props

    const showLabel = ! hideLabel || 'undefined' === typeof hideLabel;

    return(
        <>
            <div
                className="ocean-customize-control__links"
            >
                <div className={`control__links-wrap ${settingClass ? settingClass : ''}`}>
                    { !! label && showLabel &&
                        <div className="control__links--header">
                            <div className="control__links--label">
                                { titleIcon && (
                                    <span className='title--icon'>
                                        { svgIcon(titleIcon) }
                                    </span>
                                )}
                                <span className='customize-control-title'>{ label }</span>
                            </div>
                        </div>
                    }

                    { links && <LinkItems {...props}/> }
                </div>
            </div>
        </>
    );

}

export default ControlLinks;