import { __ } from '@wordpress/i18n';
import {
	BaseControl,
    Tooltip,
    Button
} from '@wordpress/components'
import { Icon, close } from '@wordpress/icons';
import FontFamily from './font-family';
import FontFormat from './font-format';
import FontColors from './font-colors';
import OceanTypoReset from './reset';
import OceanDeviceType from '../device-type';

// Style.
import './style.scss';

const OceanTypography = ( props ) => {

    const {
        modalDisplay,
        onToggleModal
    } = props;

    return(
        <BaseControl className="ocean-customize-component-typograhpy-popup ocean-component__base_control">

            { modalDisplay &&
                <div className={`typography__popup ${ modalDisplay ? 'show-popup' : '' }`}>
                    <div className='typography__popup--area'>
                        <div className="typograhpy__container--popover-content">
                            <div className='typograhpy-responsive-control-area'>

                                <div className='typograhpy-popup-toolbar'>
                                    <OceanDeviceType/>

                                    <div className='toolbar-additional'>
                                        <OceanTypoReset { ...props }/>

                                        <Tooltip
                                            text={ __( 'Close', 'ocean-extra' ) }
                                            placement="top"
                                        >
                                            <Button
                                                className="components-circular-button-icon close-typo add-buttons"
                                                onClick={ onToggleModal }
                                                aria-label={ __( 'Close', 'oceanwp' ) }
                                            >
                                                { <Icon icon={ close } /> }
                                            </Button>
                                        </Tooltip>
                                    </div>
                                </div>

                                <div className='typograhpy-popup-area'>
                                    <FontColors { ...props } />
                                    <FontFamily { ...props } />
                                    <FontFormat { ...props } />
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            }

        </BaseControl>
    )

}

export default OceanTypography;