import { __ } from '@wordpress/i18n';
import {
    BaseControl,
} from '@wordpress/components';
import {
	useRef,
	useState,
	useEffect,
	useCallback,
} from '@wordpress/element'

// Style.
import './style.scss';

const ControlRichText = ( props ) => {

    const {
        label,
        description,
        hideLabel,
        control,
        mediaButtons,
    } = props;

    let value = control.setting.get();

    const el = useRef()
    const editor = useRef(null)
	const [editorId, _] = useState(
		`${control.id}`
	)

    const correctEditor = () => wp.oldEditor || wp.editor

	const listener = useCallback(
		() => control.setting.set((correctEditor().getContent(editorId))),
		[editorId]
	)

    useEffect(() => {
        correctEditor().initialize(editorId, {
			quicktags: true,
			mediaButtons: true,
			...props,

			...(window.tinymce
				? {
						tinymce: {
							toolbar1:
								'formatselect,styleselect,bold,italic,bullist,numlist,link,alignleft,aligncenter,alignright,wp_adv',
							toolbar2:
								'strikethrough,hr,forecolor,pastetext,removeformat,charmap,outdent,indent,undo,redo,wp_help',

							...(typeof props.tinymce === 'object'
								? props.tinymce
								: {}),
							style_formats_merge: true,
							style_formats: [],
						},
				  }
				: { tinymce: null }),
		})

		if (window.tinymce) {
			setTimeout(
				() =>
					window.tinymce.editors[editorId] &&
					window.tinymce.editors[editorId].on('change', listener)
			)
		}


		if (wp.oldEditor) {
			setTimeout(
				() => {
					setTimeout(() => {
						if (
							window.tinymce &&
							window.tinymce.editors[editorId]
						) {
							window.tinymce.editors[editorId].off(
								'change',
								listener
							)
						}

						correctEditor().remove(editorId)

						correctEditor().initialize(editorId, {
							quicktags: true,
							mediaButtons: true,
							...props,

							...(window.tinymce
								? {
										tinymce: {
											toolbar1:
												'formatselect,styleselect,bold,italic,bullist,numlist,link,alignleft,aligncenter,alignright,wp_adv',
											toolbar2:
												'strikethrough,hr,forecolor,pastetext,removeformat,charmap,outdent,indent,undo,redo,wp_help',

											...(typeof props.tinymce ===
											'object'
												? props.tinymce
												: {}),
											style_formats_merge: true,
											style_formats: [],
										},
								  }
								: { tinymce: null }),
						})

						if (window.tinymce) {
							setTimeout(
								() =>
									window.tinymce.editors[editorId] &&
									window.tinymce.editors[editorId].on(
										'change',
										listener
									)
							)
						}
					}, 300)
				},

				1000
			)
		}

        return () => {
			setTimeout(() => {
				if (window.tinymce && window.tinymce.editors[editorId]) {
					window.tinymce.editors[editorId].off('change', listener)
				}

				correctEditor().remove(editorId)
			}, 300)
		}
	}, [])

	const showLabel = ! hideLabel || 'undefined' === typeof hideLabel;

	return (
		<>
			<BaseControl
				className="ocean-customize-control__rich-text"
			>
				{ !! label && showLabel &&
					<div className="control__rich-text--header">
                        <div className="control__rich-text--label">
                            <span className='customize-control-title'>{ label }</span>
                        </div>
                    </div>
				}

				{ description && (
                    <span
                        className="description customize-control-description ocean-customize-control--description"
                        dangerouslySetInnerHTML={ { __html: description } }
                    >
                    </span>
                )}

                <div className='control__rich-text--content'>
                    <textarea
                        id={editorId}
                        ref={el}
                        value={value}
                        className="wp-editor-area"
                        placeholder={ __( 'Add your content here', 'oceanwp' ) }
                        onChange={({ target: { value } }) => control.setting.set(value)}
                    />
                </div>
			</BaseControl>
		</>
	);
};

export default ControlRichText;