import { Icon, chevronRightSmall } from '@wordpress/icons';
import { decodeEntities } from '@wordpress/html-entities';
// Style.
import './style.scss';

const ControlTitle = ( props ) => {

    const {
        label,
        description,
        top ,
        bottom
    } = props;

    const style = {
        marginTop: top ? top + 'px' : '10px',
        marginBottom: bottom ? bottom + 'px' : '10px',
    };

	return (
        <div className="ocean-customize-control__title" style={style}>

            <span className='customize-control-title'>{decodeEntities(label)}</span>

            { description && (
                <span
                    className="description customize-control-description ocean-customize-control--description"
                    dangerouslySetInnerHTML={ { __html: description } }
                >
                </span>
            )}

        </div>
	);
};

export default ControlTitle;