import { __ } from '@wordpress/i18n';
import {
    BaseControl,
    SelectControl
} from '@wordpress/components';
import { decodeEntities } from '@wordpress/html-entities';

// Style.
import './style.scss';

const ControlSelect = ( props ) => {

    const {
        label,
        description,
        hideLabel,
        control,
        choices,
        multiple,
    } = props;

    let value = control.setting.get();

	const showLabel = ! hideLabel || 'undefined' === typeof hideLabel;

    const options = Object.keys(choices).map((key) => ({
        label: decodeEntities(choices[key]),
        value: key
    }));

	return (
		<>
			<BaseControl
				className="ocean-customize-control__select"
			>
				{ !! label && showLabel &&
					<div className="control__select--header">
                        <div className="control__select--label">
                            <span className='customize-control-title'>{ label }</span>
                        </div>
                    </div>
				}

                { description && (
                    <span
                        className="description customize-control-description ocean-customize-control--description"
                        dangerouslySetInnerHTML={ { __html: description } }
                    >
                    </span>
                )}


                <div className='control__select--content'>
                    <SelectControl
                        multiple={multiple}
                        value={value}
                        onChange={ ( value ) => control.setting.set(value) }
                        options={ options }
                    />
                </div>
			</BaseControl>
		</>
	);
};

export default ControlSelect;