import { __ } from '@wordpress/i18n';
import { BaseControl } from '@wordpress/components';
import { create, chevronRight } from '@wordpress/icons';
import OceanAccordion from '../../components/accordian';
import OceanRadioImage from '../../components/radio-image';

// Style.
import './style.scss';

const ControlRadioImage = ( props ) => {

    const {
        label,
        description,
        hideLabel,
        control,
        choices,
        defaultValue
    } = props;

	const showLabel = ! hideLabel || 'undefined' === typeof hideLabel;

	return (
		<>

            { description && (
                <span
                    className="description customize-control-description ocean-customize-control--description"
                    dangerouslySetInnerHTML={ { __html: description } }
                >
                </span>
            )}

			<BaseControl
				className="ocean-customize-control__radio-image"
			>
                <OceanAccordion
                    AccordionArray={ [
                        {
                            icon: chevronRight,
                            title: !! label && showLabel ? label : __( 'Layout', 'oceanwp' ),
                            content: <>
                                <div className='control__radio-image--content'>
                                    <OceanRadioImage
                                        value={ control.setting.get() || '' }
                                        onChange={ ( value ) => {
                                            control.setting.set(value);
                                        } }
                                        choices={ choices }
                                    />
                                </div>
                            </>
                        }
                    ] }
                />


			</BaseControl>
		</>
	);
};

export default ControlRadioImage;