import { __ } from '@wordpress/i18n';
import {
    ToggleControl,
    BaseControl
} from '@wordpress/components';

// Style.
import './style.scss';

function ControlSwitch( props ) {

    const {
        label,
        description,
        hideLabel,
        control
    } = props;

    let value = control.setting.get();

    const showLabel = ! hideLabel || 'undefined' === typeof hideLabel;

    return (
        <>
            { description && (
                <span
                    className="description customize-control-description ocean-customize-control--description"
                    dangerouslySetInnerHTML={ { __html: description } }
                >
                </span>
            )}

            <BaseControl
				className="ocean-customize-control__switch"
			>
                <div className='switch--container'>

                    { !! label && showLabel &&
                        <div className="control__switch--header">
                            <div className="control__switch--label">
                                <span className='customize-control-title'>{ label }</span>
                            </div>
                        </div>
                    }

                    <div className='switch--content'>

                        <ToggleControl
                            checked={ value }
                            onChange={ (value) => {
                                control.setting.set(value);
                            }}
                        />

                    </div>

                </div>
            </BaseControl>
        </>
    );
}

export default ControlSwitch;
