/* jshint esversion: 6 */

import OceanControlForm from '../controlForm';
import ControlText from './text';

export const TextControl = OceanControlForm.extend( ControlText, {
	getWrapper: function getWrapper() {
		const control = this;

		let wrapper = control.container[ 0 ];

		if ( control.params.wrapper ) {
			const wrapperElement = document.getElementById( control.params.wrapper + '--wrapper' );

			if ( wrapperElement ) {
				// Move this control into the wrapper.
				wrapper = wrapperElement;

				// Hide the original <li> container.
				control.container[ 0 ].style.display = 'none';
			}
		}

		return wrapper;
	},
} );