import svgIcon from '../../utils/icon';

// Style
import './style.scss';

const LinkItems = ( props ) => {

    const {
        links,
        target = 'blank',
        linkIcon
    } = props;

	return (
        <div className="ocean-customize-component__links">
            { links && (
                <div className="link--area">
                    { Object.keys( links ).map( (item) => {
                        const link = links[item];
                        return (
                            <div className='link--item'>

                                { linkIcon && (
                                    <span className='link--icon'>
                                        { svgIcon(linkIcon) }
                                    </span>
                                )}

                                <a key={item} href={link.url} target={'_' + target}>
                                    {link.label}
                                </a>

                            </div>
                        );
                    })}
                </div>
            )}
        </div>
	);
};

export default LinkItems;