import { __ } from '@wordpress/i18n';
import { SelectControl } from '@wordpress/components';
import Select from 'react-select';
import { useState, useEffect } from '@wordpress/element';
import systemFonts from './system-fonts.json';
import googleFonts from './google-fonts.json';

const FontFamily = (props) => {
    const [isGoogleFonts, setIsGoogleFonts] = useState(false);
    const [fontsData, setFontsData] = useState([]);
    const [getFontWeight, setGetFontWeight] = useState([]);
    const [getFontSubset, setGetFontSubset] = useState([]);

    const {
        isFontFamily = true,
        isFontWeight = true,
        isFontSubsets = true,
        control,
        deviceType,
        customizerSettings,
        fontWeight,
        fontWeightTablet,
        fontWeightMobile,
        fontFamily,
        fontSubset
    } = props;

    useEffect(() => {
        setIsGoogleFonts(wp.customize('ocean_disable_google_font').get());

        wp.customize('ocean_disable_google_font', (value) => {
            value.bind((newValue) => {
                setIsGoogleFonts(newValue);
            });
        });

        let customFonts = oceanCustomize.customFonts || [];
        let customizerFonts = oceanCustomize.customizerFonts || '[]';

        try {
            customizerFonts = JSON.parse(customizerFonts);
        } catch (e) {
            console.error('Failed to parse JSON string:', e);
            customizerFonts = [];
        }

        let fonts = [
            { value: '', label: __('Default', 'ocean') }
        ];

        if (Array.isArray(customFonts) && customFonts.length > 0) {
            fonts.push({
                label: __('Custom Fonts', 'ocean'),
                options: customFonts.map(font => ({ value: font, label: font }))
            });
        }

        if (Array.isArray(customizerFonts) && customizerFonts.length > 0) {
            customizerFonts.forEach(fontGroup => {
                fonts.push({
                    label: fontGroup.label,
                    options: fontGroup.options.map(font => {
                        return {
                            value: font.value,
                            label: font.label,
                            variants: font.variants || [],
                            subsets: font.subsets || []
                        };
                    })
                });
            });
        }

        if (Array.isArray(systemFonts) && systemFonts.length > 0) {
            fonts.push({
                label: __('System Fonts', 'ocean'),
                options: systemFonts.map(font => ({ value: font, label: font }))
            });
        }

        if (isGoogleFonts) {
            fonts.push({
                label: __('Google Fonts', 'ocean'),
                options: googleFonts.items.map(font => {
                    return {
                        value: font.family,
                        label: font.family,
                        variants: font.variants || [],
                        subsets: font.subsets || []
                    };
                })
            });
        }

        setFontsData(fonts);
    }, [customizerSettings, fontFamily, isGoogleFonts]);

    const handleFontChange = (selectedFont) => {
        control.settings[fontFamily].set(selectedFont.value);

        // Reset weights and subsets
        setGetFontWeight([]);
        setGetFontSubset([]);

        // Find the selected font in the fontsData to update weights and subsets
        fontsData.forEach(fontGroup => {
            if (Array.isArray(fontGroup.options)) {
                fontGroup.options.forEach(font => {
                    if (font.value === selectedFont.value) {
                        setGetFontWeight(font.variants || []);
                        setGetFontSubset(font.subsets || []);
                    }
                });
            }
        });
    };

    let fontWeightArray = [
        { value: '', label: __('Default', 'ocean') },
        { value: 'normal', label: __('Normal', 'ocean') },
        { value: 'bold', label: __('Bold', 'ocean') },
        { value: '100', label: '100' },
        { value: '200', label: '200' },
        { value: '300', label: '300' },
        { value: '400', label: '400' },
        { value: '500', label: '500' },
        { value: '600', label: '600' },
        { value: '700', label: '700' },
        { value: '800', label: '800' },
        { value: '900', label: '900' },
    ];

    if (getFontWeight.length > 0) {
        fontWeightArray = getFontWeight.map(k => ({ value: k, label: k }));
    }

    let fontSubsetArray = [];
    if (getFontSubset.length > 0) {
        fontSubsetArray = getFontSubset.map(k => ({ value: k, label: k }));
    }

    let fontWeightId;
    if (deviceType === 'desktop') {
        fontWeightId = fontWeight;
    } else if (deviceType === 'tablet') {
        fontWeightId = fontWeightTablet || fontWeight;
    } else if (deviceType === 'mobile') {
        fontWeightId = fontWeightMobile || fontWeightTablet || fontWeight;
    }

    const customStyles = {
        groupHeading: (provided) => ({
            ...provided,
            backgroundColor: '#e6f2ff',
            color: '#333',
            padding: '10px',
            margin: '0',
            fontWeight: 'bold',
        }),
        option: (provided, state) => ({
            ...provided,
            backgroundColor: state.isSelected ? '#f0f0f0' : state.isFocused ? '#f0f0f0' : 'transparent',
            color: state.isSelected ? '#333' : provided.color,
            '&:hover': {
                backgroundColor: '#f0f0f0',
            },
        }),
        menuPortal: base => ({ ...base, zIndex: 9999999 }),
    };

    let outputFontFamily;
    if (isFontFamily) {
        outputFontFamily = (
            <div className="typograhpy--content__font-family">
                <div className="ocean-component-label">
                    <span className="ocean-component-label--text">
                        {__('Font Family', 'ocean')}
                    </span>
                </div>
                <Select
                    options={fontsData}
                    value={{
                        value: customizerSettings[fontFamily].get() || '',
                        label: customizerSettings[fontFamily].get()
                            ? customizerSettings[fontFamily].get()
                            : __('Default', 'ocean')
                    }}
                    onChange={handleFontChange}
                    isMulti={false}
                    maxMenuHeight={300}
                    className="react-select-container"
                    classNamePrefix="react-select"
                    menuPortalTarget={document.body}
                    styles={customStyles}
                />
            </div>
        );
    }

    let outputFontWeight;
    if (isFontWeight && fontWeightArray.length > 0) {
        outputFontWeight = (
            <div className="typograhpy--content__font-weight ocean-font-object">
                <SelectControl
                    className='ocean-typo--font-weight'
                    label={__('Font Weight', 'ocean')}
                    value={customizerSettings[fontWeightId].get() || ''}
                    options={fontWeightArray}
                    onChange={(value) => {
                        control.settings[fontWeightId].set(value);
                    }}
                />
            </div>
        );
    }

    let outputFontSubsets;
    if (isFontSubsets && fontSubsetArray.length > 0) {
        outputFontSubsets = (
            <div className="typograhpy--content__font-subsets ocean-font-object">
                <SelectControl
                    label={__('Font Subset', 'ocean')}
                    value={customizerSettings[fontSubset].get() || ''}
                    options={fontSubsetArray}
                    onChange={(value) => {
                        control.settings[fontSubset].set(value);
                    }}
                />
            </div>
        );
    }

    return (
        <div className="ocean-component-typograhpy--font-family">
            {outputFontFamily}
            <div className="typograhpy--font-object">
                {outputFontWeight}
                {outputFontSubsets}
            </div>
        </div>
    );
};

export default FontFamily;
