import { __ } from '@wordpress/i18n';
import { useState, useEffect, useRef, useCallback } from '@wordpress/element';
import {
    ButtonGroup,
    Button,
    Dashicon,
    Tooltip
} from '@wordpress/components';
import './style.scss';

// Debounce function to delay validation
const debounce = (func, wait) => {
    let timeout;
    return (...args) => {
        const later = () => {
            clearTimeout(timeout);
            func(...args);
        };
        clearTimeout(timeout);
        timeout = setTimeout(later, wait);
    };
};

const OceanRangeSlider = (props) => {
    const {
        title,
        value,
        min = 0,
        max,
        step = 1,
        showUnit,
        unitValue,
        onChange,
        onChangeUnit
    } = props;

    const [currentView, setCurrentView] = useState(
        wp.customize && wp.customize.previewedDevice
            ? wp.customize.previewedDevice()
            : 'desktop'
    );

    const listener = () => {
        setCurrentView(
            wp.customize && wp.customize.previewedDevice
                ? wp.customize.previewedDevice()
                : 'desktop'
        );
    };

    useEffect(() => {
        if (!wp.customize) return;
        setTimeout(() => wp.customize.previewedDevice.bind(listener), 1000);

        return () => {
            if (!wp.customize) return;
            wp.customize.previewedDevice.unbind(listener);
        };
    }, []);

    const [unitListVisible, setUnitListVisible] = useState(false);
    const [warning, setWarning] = useState('');
    const [tempValue, setTempValue] = useState(value);
    const [tempValueTablet, setTempValueTablet] = useState(value);
    const [tempValueMobile, setTempValueMobile] = useState(value);

    const trackRef = useRef(null);
    const isDragging = useRef(false);

    useEffect(() => {
        switch (currentView) {
            case 'tablet':
                setTempValueTablet(value);
                break;
            case 'mobile':
                setTempValueMobile(value);
                break;
            default:
                setTempValue(value);
                break;
        }
    }, [currentView, value]);

    const validateValue = (val) => {
        if (isNaN(val) || val < min) return min;
        if (val > max) return max;
        return val;
    };

    const onTrackMouseDown = (event) => {
        const trackRect = trackRef.current.getBoundingClientRect();
        const clickPosition = event.clientX - trackRect.left;
        const relativePosition = clickPosition / trackRect.width;
        const valueRange = max - min;
        const totalSteps = valueRange / step;
        const steps = Math.round(relativePosition * totalSteps);
        const calculatedValue = parseFloat((min + steps * step).toFixed(1));
        const validatedValue = validateValue(calculatedValue);

        if (validatedValue >= min && validatedValue <= max) {
            updateValueBasedOnCurrentView(validatedValue);
            onChange(validatedValue);
        }

        isDragging.current = true;
        document.addEventListener('mousemove', handleDrag);
        document.addEventListener('mouseup', handleDragEnd);
    };

    const handleDrag = (event) => {
        if (isDragging.current) {
            onTrackMouseDown(event);
        }
    };

    const handleDragEnd = () => {
        isDragging.current = false;
        document.removeEventListener('mousemove', handleDrag);
        document.removeEventListener('mouseup', handleDragEnd);
    };

    const handleChange = (event) => {
        const newValue = event.target.value;
        updateValueBasedOnCurrentView(newValue);
        debouncedValidate(newValue);
    };

    const handleBlur = (event) => {
        const targetValue = parseFloat(event.target.value);
        const validatedValue = validateValue(targetValue);
        updateValueBasedOnCurrentView(validatedValue);
        onChange(validatedValue);
    };

    const onSliderChange = (event) => {
        const newValue = parseFloat(event.target.value);
        const validatedValue = validateValue(newValue);
        updateValueBasedOnCurrentView(validatedValue);
        onChange(validatedValue);
    };

    const onInputKeyDown = (event) => {
        if (event.keyCode === 13) {
            event.preventDefault();
            event.target.blur();
        }
    };

    const onUnitChange = (selectedUnit) => {
        onChangeUnit(selectedUnit);
        setUnitListVisible(false);
    };

    const toggleUnitList = () => {
        setUnitListVisible(!unitListVisible);
    };

    const updateValueBasedOnCurrentView = (value) => {
        switch (currentView) {
            case 'tablet':
                setTempValueTablet(value);
                break;
            case 'mobile':
                setTempValueMobile(value);
                break;
            default:
                setTempValue(value);
                break;
        }
    };

    const debouncedValidate = useCallback(debounce((value) => {
        const parsedValue = parseFloat(value);
        const validatedValue = validateValue(parsedValue);
        switch (currentView) {
            case 'tablet':
                setTempValueTablet(validatedValue);
                break;
            case 'mobile':
                setTempValueMobile(validatedValue);
                break;
            default:
                setTempValue(validatedValue);
                break;
        }
        onChange(validatedValue);
    }, 500), [currentView, onChange]);

    let displayValue;
    switch (currentView) {
        case 'tablet':
            displayValue = tempValueTablet;
            break;
        case 'mobile':
            displayValue = tempValueMobile;
            break;
        default:
            displayValue = tempValue;
            break;
    }

    let stylewidth = `${((validateValue(displayValue) - min) / (max - min)) * 100}%`;

    return (
        <div className='ocean-customize-component-range-slider'>
            {title && (
                <div className="ocean-component-label">
                    <span className="ocean-component-label--text">{title}</span>
                </div>
            )}

            <div className='range-slider__container'>
                <div className='range-slider--slider'>
                    <input
                        className='range-slider--range-input'
                        type="range"
                        min={min}
                        max={max}
                        step={step}
                        value={validateValue(displayValue)}
                        onChange={onSliderChange}
                        onKeyDown={onInputKeyDown}
                    />
                    <div
                        className='range-slider--track'
                        ref={trackRef}
                        onMouseDown={onTrackMouseDown}
                    >
                        <div style={{ 'width': stylewidth }}></div>
                        <span style={{ '--cssPosition': stylewidth }}></span>
                    </div>
                </div>

                <div className='range-slider--input'>
                    <input
                        type="number"
                        min={min}
                        max={max}
                        step={step}
                        value={displayValue}
                        onChange={handleChange}
                        onKeyDown={onInputKeyDown}
                        onBlur={handleBlur}
                    />

                    {showUnit && (
                        <>
                            <span
                                className='range-slider--unit'
                                onClick={toggleUnitList}
                            >
                                {unitValue ? unitValue : '--'}
                            </span>

                            <div className={`range-slider--unit-list ${unitListVisible ? 'show' : ''}`}>
                                <span className='unit-option' onClick={() => { onUnitChange('px'), setUnitListVisible(false) }}>px</span>
                                <span className='unit-option' onClick={() => { onUnitChange('em'), setUnitListVisible(false) }}>em</span>
                                <span className='unit-option' onClick={() => { onUnitChange('rem'), setUnitListVisible(false) }}>rem</span>
                                <span className='unit-option' onClick={() => { onUnitChange('%'), setUnitListVisible(false) }}>%</span>
                                <span className='unit-option' onClick={() => { onUnitChange('pt'), setUnitListVisible(false) }}>pt</span>
                                <span className='unit-option' onClick={() => { onUnitChange(''), setUnitListVisible(false) }}>--</span>
                            </div>
                        </>
                    )}
                </div>
            </div>

            {warning && (
                <div className='ocean-customize-notice is-warning'>{warning}</div>
            )}
        </div>
    );
};

export default OceanRangeSlider;
