import { __ } from '@wordpress/i18n';
import { BaseControl } from '@wordpress/components';
import { useState, useEffect } from '@wordpress/element';
import OceanRangeSlider from '../../components/range-slider';
import OceanDeviceType from '../../components/device-type';

// Style.
import './style.scss';

const ControlRangeSlider = ( props ) => {

    const {
        label,
        description,
        hideLabel,
        customizerSettings,
        control,
        settingGroup,
        min,
        max,
        step,
        isUnit = false,
        isResponsive = false,
        defaultValue
    } = props;

	const showLabel = ! hideLabel || 'undefined' === typeof hideLabel;

    const settingsArray = Object.values(settingGroup);

    const [currentView, setCurrentView] = useState(
		wp.customize && wp.customize.previewedDevice
			? wp.customize.previewedDevice()
			: 'desktop'
	)

	const listener = () => {
		setCurrentView(
			wp.customize && wp.customize.previewedDevice
				? wp.customize.previewedDevice()
				: 'desktop'
		)
	}

    useEffect(() => {
		if (!wp.customize) return
		setTimeout(() => wp.customize.previewedDevice.bind(listener), 1000)

		return () => {
			if (!wp.customize) return
			wp.customize.previewedDevice.unbind(listener)
		}
	}, [])

    const renderRangeSlider = (element) => {
        if ( isUnit ) {
            return(
                <div className="control__range_slider--slider has-unit--flex">
                    <OceanRangeSlider
                        value={ customizerSettings[element.id].get() }
                        onChange={ ( value ) => {
                            control.settings[element.id].set( value );
                        } }
                        showUnit={ isUnit }
                        unitValue={ customizerSettings['unit'].get() }
                        onChangeUnit={ ( value ) => {
                            control.settings['unit'].set( value );
                        } }
                        min={ min }
                        max={ max }
                        step={ step }
                    />
                </div>
            )
        } else {
            return(
                <div className="control__range_slider--slider">
                    <OceanRangeSlider
                        value={ customizerSettings[element.id].get() }
                        onChange={ ( value ) => {
                            control.settings[element.id].set( value );
                        } }
                        showUnit={ isUnit }
                        min={ min }
                        max={ max }
                        step={ step }
                    />
                </div>
            )
        }
    };

	return (
		<>
			<BaseControl
				className="ocean-customize-control__range_slider"
			>
				{ !! label && showLabel &&
					<div className="control__range_slider--header">
                        <div className="control__range_slider--label">
                            <span className='customize-control-title'>{ label }</span>
                        </div>
                        { isResponsive && <OceanDeviceType/> }
                    </div>
				}

                { description && (
                    <span
                        className="description customize-control-description ocean-customize-control--description"
                        dangerouslySetInnerHTML={ { __html: description } }
                    >
                    </span>
                )}

                <div className='control__range_slider--content'>
                    { settingsArray.map((element) =>
                        isResponsive ? (
                            currentView === element.id && renderRangeSlider(element)
                        ) : (
                            renderRangeSlider(element)
                        )
                    )}
                </div>
			</BaseControl>
		</>
	);
};

export default ControlRangeSlider;