import { __ } from '@wordpress/i18n';
import {
    Button,
    ButtonGroup,
    BaseControl
} from '@wordpress/components';
import { useState } from '@wordpress/element';
import { useSelect } from "@wordpress/data";
import { Icon, image as addImage } from "@wordpress/icons";

// Style.
import './style.scss';

function ControlImagePicker( props ) {

    const {
        label,
        description,
        hideLabel,
        mediaType = 'image',
        control
    } = props;

    let value = control.setting.get();

    const showLabel = ! hideLabel || 'undefined' === typeof hideLabel;

    const [mediaId, setMediaId] = useState(value ? value : 0);

    const openMediaUploader = () => {
        const frame = wp.media({
            title: 'Select ' + mediaType,
            multiple: false,
            library: { type: mediaType },
            button: { text: 'Add' },
        });

        frame.on('select', () => {
            const selection = frame.state().get('selection');
            if (selection.length > 0) {
                const attachment = selection.first().toJSON();
                control.setting.set(attachment.id);
                setMediaId(attachment.id);
            }
        });

        frame.open();
    };

    const { image } = useSelect( select => {

        if ( mediaId !== 0 ) {
            const img = select( 'core' ).getMedia( mediaId )
            if( img ) {
                return {
                    image: img,
                };
            }
        }

        return {
            image: undefined,
        };

	})

    const removeMedia = () => {
        control.setting.set('');
        setMediaId(0);
    }

    return (
        <>
            <BaseControl
				className="ocean-customize-control__image-picker"
			>
                <div className='image-picker--container'>

                    { !! label && showLabel &&
                        <div className="control__image-picker--header">
                            <div className="control__image-picker--label">
                                <span className='customize-control-title'>{ label }</span>
                            </div>
                        </div>
                    }

                    { description && (
                        <span
                            className="description customize-control-description ocean-customize-control--description"
                            dangerouslySetInnerHTML={ { __html: description } }
                        >
                        </span>
                    )}

                    <div className='image-picker--content'>

                        { !! image ?
                            <div className='image-picker--image'>
                                <img
                                    src={ image.source_url }
                                    alt={ image.alt ? image.alt_text : image.title.rendered }
                                />
                            </div>
                        :
                            <div
                                className='image-picker--placeholder'
                                onClick={openMediaUploader}
                            >
                                { <Icon icon={addImage} fill={"currentColor"}/> }
                            </div>
                        }

                        <div className='image-picker--buttons'>
                            <ButtonGroup>
                                <Button
                                    onClick={openMediaUploader}
                                    variant='secondary'
                                >
                                    { mediaId !== 0 ? __( 'Replace', 'oceanwp' ) : __( 'Add', 'oceanwp' ) }
                                </Button>

                                { mediaId !== 0 &&
                                    <Button
                                        onClick={removeMedia}
                                        variant='secondary'
                                        isDestructive
                                    >
                                        { __( 'Remove', 'oceanwp' ) }
                                    </Button>
                                }
                            </ButtonGroup>
                        </div>

                    </div>

                </div>
            </BaseControl>
        </>
    );
}

export default ControlImagePicker;
