import { __ } from '@wordpress/i18n';
import { BaseControl } from '@wordpress/components';
import { Icon, arrowLeft } from '@wordpress/icons';
import { useState } from '@wordpress/element';
import OceanColorPicker from '../color-picker';

const FontColors = ( props ) => {

    const {
        customizerSettings,
        control,
        textColor,
        textColorHover,
        defaultValue
    } = props;

    const [openPickerIndex, setOpenPickerIndex] = useState(null);

    let colorPickerContent = (
        <div className='color-picker-inline-wrap'>

            { !! customizerSettings[textColor] &&
                <OceanColorPicker
                    value={ customizerSettings[textColor].get() }
                    tooltip={ __( 'Color', 'oceanwp' ) }
                    tooltipPosition={ 'top center' }
                    showPalette={ true }
                    modalStyle="style-inline"
                    modalDisplay={openPickerIndex === textColor}
                    onToggleModal={() => {
                        setOpenPickerIndex(openPickerIndex === textColor ? null : textColor);
                    }}
                    onChange={ ( color ) => {
                        control.settings[textColor].set( color );
                    } }
                    onClickReset={ () => {
                        control.settings[textColor].set( defaultValue );
                    } }
                />
            }

            { !! customizerSettings[textColorHover] &&
                <OceanColorPicker
                    value={ customizerSettings[textColorHover].get() }
                    tooltip={ __( 'Hover Color', 'oceanwp' ) }
                    tooltipPosition={ 'top center' }
                    showPalette={ true }
                    modalStyle="style-inline"
                    modalDisplay={openPickerIndex === textColorHover}
                    onToggleModal={() => {
                        setOpenPickerIndex(openPickerIndex === textColorHover ? null : textColorHover);
                    }}
                    onChange={ ( color ) => {
                        control.settings[textColorHover].set( color );
                    } }
                    onClickReset={ () => {
                        control.settings[textColorHover].set( defaultValue );
                    } }
                />
            }
        </div>
    )

	return (
        <>
            { ( !! customizerSettings[textColor] || !! customizerSettings[textColorHover] ) &&
                <div className="ocean-customize-typography__color-picker">
                    <div className="control__color-picker--label">
                        { !! openPickerIndex ?
                            <span
                                className='color-label'
                                onClick={ () => setOpenPickerIndex(null) }
                            >
                                <Icon icon={arrowLeft}/>
                                { __( 'Back', 'oceanwp' ) }
                            </span>
                        :
                            <span className='customize-control-title'>{ __( 'Color', 'oceanwp' ) }</span>
                        }
                    </div>

                    <div className='control__color-picker--button-wrap'>
                        { colorPickerContent}
                    </div>
                </div>
            }
        </>
	);
};

export default FontColors;