import { __ } from '@wordpress/i18n';
import {
	ButtonGroup,
	Tooltip,
	Button
} from '@wordpress/components';
import { Icon, globe } from '@wordpress/icons';
import OceanRangeSlider from '../../components/range-slider';

const FontFormat = ( props ) => {

    let outputFontSize,
        outputTextTransform,
        outputLineHeight,
        outputLetterSpacing,
        outputTextDecoration;

    const {
        isFontSize = true,
        isTextTransform = true,
        isLineHeight = true,
        isLetterSpacing = true,
        control,
        deviceType,
        customizerSettings,
        fontSize,
        fontSizeTablet,
        fontSizeMobile,
        fontSizeUnit,
        lineHeight,
        lineHeightTablet,
        lineHeightMobile,
        lineHeightUnit,
        letterSpacing,
        letterSpacingTablet,
        letterSpacingMobile,
        letterSpacingUnit,
        textTransform,
        textTransformTablet,
        textTransformMobile,
        textDecoration
    } = props;

    const textTransformOptions = [
        {
            name: 'Global',
            key: '',
            symbol: <Icon icon={ globe } />,
            class: 'ocean-editor-control-button',
        },
        {
            name: 'None',
            key: 'none',
            symbol: 'X',
            class: 'ocean-editor-control-button',
        },
        {
            name: 'Uppercase',
            key: 'uppercase',
            symbol: 'AA',
            class: 'ocean-editor-control-button',
        },
        {
            name: 'Capitalize',
            key: 'capitalize',
            symbol: 'Aa',
            class: 'ocean-editor-control-button',
        },
        {
            name: 'Lowercase',
            key: 'lowercase',
            symbol: 'aa',
            class: 'ocean-editor-control-button',
        },
    ];

    const textDecorationOptions = [
        {
            name: 'Global',
            key: '',
            symbol: <Icon icon={ globe } />,
            class: 'ocean-editor-control-button',
        },
        {
            name: 'None',
            key: 'none',
            symbol: 'X',
            class: 'ocean-editor-control-button',
        },
        {
            name: 'Overline',
            key: 'overline',
            symbol: 'Aa',
            class: 'ocean-editor-control-button td-overline',
        },
        {
            name: 'Underline',
            key: 'underline',
            symbol: 'Aa',
            class: 'ocean-editor-control-button td-underline',
        },
        {
            name: 'Line Tthrough',
            key: 'line-through',
            symbol: 'Aa',
            class: 'ocean-editor-control-button td-line-through',
        },
        {
            name: 'Underline Overline',
            key: 'underline overline',
            symbol: 'Aa',
            class: 'ocean-editor-control-button td-underline-overline',
        },
    ];

    let fontSizeId,
        lineHeightId,
        letterSpacingId,
        textTransformId;

    const getValue = ( key, defaultValue = '' ) => {
        let result = customizerSettings[key].get() ? customizerSettings[key].get() : defaultValue;
        return result;
    }

    if ( 'desktop' === deviceType ) {

        // Font size.
        fontSizeId = fontSize;

        // Text transform.
        textTransformId = textTransform;

        // Line Height.
        lineHeightId = lineHeight;

        // Letter spacing.
        letterSpacingId = letterSpacing;

    } else if ( 'tablet' === deviceType ) {

        // Font size.
        fontSizeId = fontSizeTablet ? fontSizeTablet : fontSize;

        // Text transform.
        textTransformId = textTransformTablet ? textTransformTablet : textTransform;

        // Line Height.
        lineHeightId = lineHeightTablet ? lineHeightTablet : lineHeight;

        // Letter spacing.
        letterSpacingId = letterSpacingTablet ? letterSpacingTablet : letterSpacing;

    } else if ( 'mobile' === deviceType ) {

        // Font size.

        if ( getValue( fontSizeTablet, null ) ) {
            fontSizeId = fontSizeMobile ? fontSizeMobile : fontSizeTablet;
        } else {
            fontSizeId = fontSizeMobile ? fontSizeMobile : fontSize;
        }

        // Text transform.
        if ( getValue( textTransformTablet, '' ) ) {
            textTransformId = textTransformMobile ? textTransformMobile : textTransformTablet;
        } else {
            textTransformId = textTransformMobile ? textTransformMobile : textTransform;
        }

        // Line Height.
        if ( getValue( lineHeightTablet, null ) ) {
            lineHeightId = lineHeightMobile ? lineHeightMobile : lineHeightTablet;
        } else {
            lineHeightId = lineHeightMobile ? lineHeightMobile : lineHeight;
        }

        // Letter spacing.
        if ( getValue( letterSpacingTablet, null ) ) {
            letterSpacingId = letterSpacingMobile ? letterSpacingMobile : letterSpacingTablet;
        } else {
            letterSpacingId = letterSpacingMobile ? letterSpacingMobile : letterSpacing;
        }
    }

    if ( false !== isFontSize ) {
        outputFontSize = (
            <div className="typograhpy--content__font-size control__range_slider--slider has-unit--flex">
                <OceanRangeSlider
                    title={ __( 'Font Size', 'oceanwp') }
                    value={ getValue( fontSizeId, null ) }
                    onChange={ ( value ) => {
                        control.settings[fontSizeId].set( value );
                    } }
                    showUnit={ true }
                    unitValue={ getValue( fontSizeUnit, '' ) }
                    onChangeUnit={ ( value ) => {
                        control.settings[fontSizeUnit].set( value );
                    } }
                    min={ 0 }
                    max={ 180 }
                    step={ 1 }
                />
            </div>
        )
    }

    if ( false !== isTextTransform ) {
        outputTextTransform = (
            <div className="ocean-typograhpy--content__text-transform">
                <ButtonGroup className="ocean-component-button-group">
                    <div className="ocean-component-button-group--label ocean-component-label">
                        <span className="ocean-component-label--text">{ __( 'Text Transform', 'oceanwp' ) }</span>
                    </div>
                    <div className="ocean-component-button-group__buttons">
                        { textTransformOptions.map( ( option ) => {
                            return(
                                <Tooltip
                                    /* translators: option name */
                                    text={ sprintf( __( '%s', 'oceanwp' ), option.name ) }
                                    key={ option.key}
                                    placement="top"
                                >
                                    <Button
                                        key={ option.key }
                                        className={ `components-button components-tab-panel__tabs-item ${ option.class }${ option.key === getValue( textTransformId, '' ) ? ' active-tab' : '' }` }
                                        aria-pressed={ option.key === getValue( textTransformId, '' ) }
                                        variant={ ( option.key === getValue( textTransformId, '' ) ) ? 'primary' : 'secondary' }
                                        onClick={ () => {
                                            control.settings[textTransformId].set( option.key );
                                        } }
                                    >
                                        { option.symbol }
                                    </Button>
                                </Tooltip>
                            )
                        } ) }
                    </div>
                </ButtonGroup>
            </div>
        )
    }

    if ( false !== isLineHeight ) {
        outputLineHeight = (
            <div className="typograhpy--content__line-height control__range_slider--slider has-unit--flex">
                <OceanRangeSlider
                    title={ __( 'Line Height', 'oceanwp') }
                    value={ getValue( lineHeightId, null ) }
                    onChange={ ( value ) => {
                        control.settings[lineHeightId].set( value );
                    } }
                    showUnit={ true }
                    unitValue={ getValue( lineHeightUnit, '' ) }
                    onChangeUnit={ ( value ) => {
                        control.settings[lineHeightUnit].set( value );
                    } }
                    min={ 0 }
                    max={ 100 }
                    step={ 0.1 }
                />
            </div>
        )
    }

    if ( false !== isLetterSpacing ) {
        outputLetterSpacing = (
            <div className="typograhpy--content__letter-spacing control__range_slider--slider has-unit--flex">
                <OceanRangeSlider
                    title={ __( 'Letter Spacing', 'oceanwp') }
                    value={ getValue( letterSpacingId, null ) }
                    onChange={ ( value ) => {
                        control.settings[letterSpacingId].set( value );
                    } }
                    showUnit={ true }
                    unitValue={ getValue( letterSpacingUnit, '' ) }
                    onChangeUnit={ ( value ) => {
                        control.settings[letterSpacingUnit].set( value );
                    } }
                    min={ -10 }
                    max={ 10 }
                    step={ 0.1 }
                />
            </div>
        )
    }

    if ( '' !== textDecoration ) {
        outputTextDecoration = (
            <div className="ocean-typograhpy--content__text-decoration">
                <ButtonGroup className="ocean-component-button-group">
                    <div className="ocean-component-button-group--label ocean-component-label">
                        <span className="ocean-component-label--text">{ __( 'Text Decoration', 'oceanwp' ) }</span>
                    </div>
                    <div className="ocean-component-button-group__buttons">
                        { textDecorationOptions.map( ( option ) => {
                            return(
                                <Tooltip
                                    /* translators: option name */
                                    text={ sprintf( __( '%s', 'oceanwp' ), option.name ) }
                                    key={ option.key}
                                >
                                    <Button
                                        key={ option.key }
                                        className={ `components-button components-tab-panel__tabs-item ${ option.class }${ option.key === getValue( textDecoration, '' ) ? ' active-tab' : '' }` }
                                        aria-pressed={ option.key === getValue( textDecoration, '' ) }
                                        variant={ ( option.key === getValue( textDecoration, '' ) ) ? 'primary' : 'secondary' }
                                        onClick={ () => {
                                            control.settings[textDecoration].set( option.key );
                                        } }
                                    >
                                        { option.symbol }
                                    </Button>
                                </Tooltip>
                            )
                        } ) }
                    </div>
                </ButtonGroup>
            </div>
        )
    }

    return(
        <div className="typograhpy--font-formats">
            { outputFontSize }
            { outputTextTransform }
            { outputLineHeight }
            { outputLetterSpacing }
            { outputTextDecoration }
        </div>
    );
}

export default FontFormat;