import { __ } from '@wordpress/i18n';
import {
	ButtonGroup,
	Tooltip,
	Button,
    BaseControl
} from '@wordpress/components';

// Style
import './style.scss';

const ControlButtonGroup = ( props ) => {

    const {
        label,
        description,
        control,
        choices,
        hideLabel,
        wrap = false,
    } = props

    let value = control.setting.get();

    const showLabel = ! hideLabel || 'undefined' === typeof hideLabel;

    return(
        <>

            <BaseControl
				className="ocean-customize-control__button-group"
			>
				{ !! label && showLabel &&
					<div className="control__button-group--header">
                        <div className="control__button-group--label">
                            <span className='customize-control-title'>{ label }</span>
                        </div>
                    </div>
				}

                { description && (
                    <span
                        className="description customize-control-description ocean-customize-control--description"
                        dangerouslySetInnerHTML={ { __html: description } }
                    >
                    </span>
                )}

                <ButtonGroup className="ocean-component-button-group">
                    <div className={`ocean-component-button-group__buttons ${ wrap ? 'is-wrap' : '' }`}>
                        { Object.values(choices).map((choice, index) => {
                            return(
                                <Tooltip
                                    /* translators: option name */
                                    text={ sprintf( __( '%s', 'ocean-extra' ), choice.label ) }
                                    key={ index }
                                    placement="top"
                                >
                                    <Button
                                        key={ index }
                                        className={ `components-button components-tab-panel__tabs-item ${choice.id} ${ choice.id === value ? ' active-tab' : '' }` }
                                        aria-pressed={ choice.id === value }
                                        variant={ ( choice.id === value ) ? 'primary' : 'secondary' }
                                        onClick={ () => {
                                            control.setting.set( choice.id );
                                        } }
                                    >
                                        <span
                                            className="content_buttongroup--button"
                                            dangerouslySetInnerHTML={{ __html: choice.content }}
                                        ></span>
                                    </Button>
                                </Tooltip>
                            )
                        })}
                    </div>
                </ButtonGroup>
            </BaseControl>
		</>
    );

}


export default ControlButtonGroup;