import './style.scss';

const ControlDivider = ( props ) => {

    const {
        top,
        bottom
    } = props;

    const style = {
        marginTop: top ? top + 'px' : '20px',
        marginBottom: bottom ? bottom + 'px' : '20px',
    };

    return (
        <div className='ocean-customize-control__divider'>
            <span
                className="control__divider--span"
                style={(!!top || !!bottom) ? style : null}
            >
            </span>
        </div>
    );
};

export default ControlDivider;
