import { __ } from '@wordpress/i18n';
import { BaseControl } from '@wordpress/components';
import { useState, useEffect } from '@wordpress/element';
import OceanSpacing from '../../components/spacing'
import OceanDeviceType from '../../components/device-type';

// Style.
import './style.scss';

const ControlSpacing = ( props ) => {

    const {
        label,
        description,
        hideLabel,
        isType,
		isTop = true,
        isRight = true,
        isBottom = true,
        isLeft = true,
		isResponsive = true,
    } = props;

	const showLabel = ! hideLabel || 'undefined' === typeof hideLabel;

    const [currentView, setCurrentView] = useState(
		wp.customize && wp.customize.previewedDevice
			? wp.customize.previewedDevice()
			: 'desktop'
	)

	const listener = () => {
		setCurrentView(
			wp.customize && wp.customize.previewedDevice
				? wp.customize.previewedDevice()
				: 'desktop'
		)
	}

    useEffect(() => {
		if (!wp.customize) return
		setTimeout(() => wp.customize.previewedDevice.bind(listener), 1000)

		return () => {
			if (!wp.customize) return
			wp.customize.previewedDevice.unbind(listener)
		}
	}, [])

	return (
		<>
			<BaseControl
				className="ocean-customize-control__spacing"
			>
				{ !! label && showLabel &&
					<div className="control__spacing--header">
						<div className="control__spacing--label">
							<span className='customize-control-title'>{ label }</span>
						</div>
						{ isResponsive && <OceanDeviceType/> }
					</div>
				}

				{ description && (
                    <span
                        className="description customize-control-description ocean-customize-control--description"
                        dangerouslySetInnerHTML={ { __html: description } }
                    >
                    </span>
                )}

                <OceanSpacing { ...props }
                    deviceType={ currentView }
                    isType={ isType }
					isTop={ isTop }
					isRight={ isRight }
					isBottom={ isBottom }
					isLeft={ isLeft }
                    spacingTop={ 'spacingTop' }
                    spacingRight={ 'spacingRight' }
                    spacingBottom={ 'spacingBottom' }
                    spacingLeft={ 'spacingLeft' }
                    spacingTopTablet={ 'spacingTopTablet' }
                    spacingRightTablet={ 'spacingRightTablet' }
                    spacingBottomTablet={ 'spacingBottomTablet' }
                    spacingLeftTablet={ 'spacingLeftTablet' }
                    spacingTopMobile={ 'spacingTopMobile' }
                    spacingRightMobile={ 'spacingRightMobile' }
                    spacingBottomMobile={ 'spacingBottomMobile' }
                    spacingLeftMobile={ 'spacingLeftMobile' }
                />
			</BaseControl>
		</>
	);
};

export default ControlSpacing;