import { __ } from '@wordpress/i18n';
import {
    BaseControl,
    Button
} from '@wordpress/components';
import { useState, useEffect } from '@wordpress/element';
import { Icon, dragHandle, chevronRight, link, linkOff } from '@wordpress/icons';
import SortableList, { SortableItem } from 'react-easy-sort';
import { arrayMoveImmutable } from 'array-move';
import OceanAccordion from '../../components/accordian';

// Style.
import './style.scss';

function ControlSortable( props ) {

    const {
        label,
        description,
        hideLabel,
        control,
        choices,
    } = props;

    // Decode JSON string to array if necessary
    let value = control.setting.get();
    if (typeof value === 'string') {
        try {
            value = JSON.parse(value);
        } catch (e) {
            console.error('Failed to parse JSON string:', e);
            value = [];
        }
    }

    const [items, setItems] = useState(value);

    const onSortEnd = ( oldIndex, newIndex ) => {
        setItems( (array) => arrayMoveImmutable( array, oldIndex, newIndex ) );
    };

    const toggleItem = (item) => {
        if (item !== undefined) {
            if (items.includes(item)) {
                setItems((prevItems) => prevItems.filter((i) => i !== item));
            } else {
                setItems((prevItems) => [...prevItems, item]);
            }
        }
    }

    useEffect(() => {
        control.setting.set(JSON.stringify(items));
    }, [items]);

    const showLabel = ! hideLabel || 'undefined' === typeof hideLabel;

    const orderedChoicesWithKeys = Object.keys(choices)
    .map((key) => ({
        key,
        label: choices[key],
    }))
    .sort((a, b) => {
        const indexA = items.indexOf(a.key);
        const indexB = items.indexOf(b.key);

        if (indexA === -1 && indexB === -1) {
            return 0;
        } else if (indexA === -1) {
            return 1;
        } else if (indexB === -1) {
            return -1;
        }

        return indexA - indexB;
    });

    return (
        <>
            { description && (
                <span
                    className="description customize-control-description ocean-customize-control--description"
                    dangerouslySetInnerHTML={ { __html: description } }
                >
                </span>
            )}

            <BaseControl
				className="ocean-customize-control__sortable"
			>
                <OceanAccordion  {...props}
                    AccordionArray={ [
                        {
                            icon: chevronRight,
                            title: !! label && showLabel ? label : __( 'Elements Position', 'oceanwp' ),
                            content: <>
                                 <div className='sortable--container'>
                                    <SortableList
                                        onSortEnd={onSortEnd}
                                        className="ocean-customize-control-sortable--list"
                                        draggedItemClassName="dragged"
                                    >
                                        {orderedChoicesWithKeys.map(({ key, label }) => (
                                            <SortableItem key={key}>
                                                <div
                                                    className={`ocean-customize-control-sortable--item ${items.includes(key) ? '' : 'disabled'}`}
                                                >
                                                    <div className='item--inner'>
                                                        <Button
                                                            className='item-icon-display'
                                                            onClick={() => toggleItem(key)}
                                                        >
                                                            {items.includes(key) ? <Icon icon={link}/> : <Icon icon={linkOff}/>}
                                                        </Button>
                                                        <span className='item-label'>{label}</span>
                                                    </div>
                                                    { items.includes(key) &&
                                                        <span className='item-icon-drag'>
                                                            <Icon icon={dragHandle}/>
                                                        </span>
                                                    }
                                                </div>
                                            </SortableItem>
                                        ))}
                                    </SortableList>
                                 </div>
                            </>
                        }
                    ] }
                />
            </BaseControl>
        </>
    );
}

export default ControlSortable;
