import { Icon, help } from '@wordpress/icons';
import svgIcon from '../../utils/icon';

const CustomContent = ( props ) => {

    const {
        isContent,
        settingClass
    } = props;

	return (
        <div className="ocean-customize-component__custom-content">
            { isContent && (
                <div className="custom-content--area">
                    <div
                        className='is-content'
                        dangerouslySetInnerHTML={{ __html: isContent }}
                    ></div>
                    { 'need-help' === settingClass && (
                        svgIcon('help-1')
                    )}
                </div>
            )}
        </div>
	);
};

export default CustomContent;