import { __ } from '@wordpress/i18n';
import { useState } from '@wordpress/element';
import {
	Button,
	Tooltip
} from '@wordpress/components'
import { Icon, lock, unlock } from '@wordpress/icons';


// Style.
import './style.scss';

const OceanSpacing = ( props ) => {

    const {
        label,
        control,
        isType,
        isTop,
        isRight,
        isBottom,
        isLeft,
        deviceType,
        spacingTop,
        spacingRight,
        spacingBottom,
        spacingLeft,
        spacingTopTablet,
        spacingRightTablet,
        spacingBottomTablet,
        spacingLeftTablet,
        spacingTopMobile,
        spacingRightMobile,
        spacingBottomMobile,
        spacingLeftMobile,
        customizerSettings
    } = props;

    let spacingTopId = '',
        spacingRightId = '',
        spacingBottomId = '',
        spacingLeftId = '',
        spacingTopPlaceholder = '',
        spacingRightPlaceholder = '',
        spacingBottomPlaceholder = '',
        spacingLeftPlaceholder = '';

    const[spacingLock, setSpacingLock] = useState(false);

    const toggleSpacingLock = () => {
        setSpacingLock( ! spacingLock );
    };

    const getValue = ( key, isExist, defaultValue = '' ) => {
        if (isExist) {
            return key && customizerSettings[key]?.get() ? customizerSettings[key].get() : defaultValue;
        }
    }

    if ( 'desktop' === deviceType ) {

        spacingTopId    = spacingTop;
        spacingRightId  = spacingRight;
        spacingBottomId = spacingBottom;
        spacingLeftId   = spacingLeft;

    } else if ( 'tablet' === deviceType ) {

        spacingTopId    = spacingTopTablet ? spacingTopTablet : spacingTop;
        spacingRightId  = spacingRightTablet ? spacingRightTablet : spacingRight;
        spacingBottomId = spacingBottomTablet ? spacingBottomTablet : spacingBottom;
        spacingLeftId   = spacingLeftTablet ? spacingLeftTablet : spacingLeft;

        spacingTopPlaceholder = getValue( spacingTopTablet, isTop ) ? spacingTopTablet : spacingTop;
        spacingRightPlaceholder = getValue( spacingRightTablet, isRight ) ? spacingRightTablet : spacingRight;
        spacingBottomPlaceholder = getValue( spacingBottomTablet, isBottom ) ? spacingBottomTablet : spacingBottom;
        spacingLeftPlaceholder = getValue( spacingLeftTablet, isLeft ) ? spacingLeftTablet : spacingLeft;

    } else if ( 'mobile' === deviceType ) {

        if ( getValue( spacingTopTablet ) ) {
            spacingTopId = spacingTopMobile ? spacingTopMobile : spacingTopTablet;
            spacingTopPlaceholder = getValue( spacingTopMobile, isTop ) ? spacingTopMobile : spacingTopTablet;
        } else {
            spacingTopId = spacingTopMobile ? spacingTopMobile : spacingTop;
            spacingTopPlaceholder = getValue( spacingTopMobile, isTop ) ? spacingTopMobile : spacingTop;
        }

        if ( getValue( spacingRightTablet ) ) {
            spacingRightId = spacingRightMobile ? spacingRightMobile : spacingRightTablet;
            spacingRightPlaceholder = getValue( spacingRightMobile, isRight ) ? spacingRightMobile : spacingRightTablet;
        } else {
            spacingRightId = spacingRightMobile ? spacingRightMobile : spacingRight;
            spacingRightPlaceholder = getValue( spacingRightMobile, isRight ) ? spacingRightMobile : spacingRight;
        }

        if ( getValue( spacingBottomTablet ) ) {
            spacingBottomId = spacingBottomMobile ? spacingBottomMobile : spacingBottomTablet;
            spacingBottomPlaceholder = getValue( spacingBottomMobile, isBottom ) ? spacingBottomMobile : spacingBottomTablet;
        } else {
            spacingBottomId = spacingBottomMobile ? spacingBottomMobile : spacingBottom;
            spacingBottomPlaceholder = getValue( spacingBottomMobile, isBottom ) ? spacingBottomMobile : spacingBottom;
        }

        if ( getValue( spacingLeftTablet ) ) {
            spacingLeftId = spacingLeftMobile ? spacingLeftMobile : spacingLeftTablet;
            spacingLeftPlaceholder = getValue( spacingLeftMobile, isLeft ) ? spacingLeftMobile : spacingLeftTablet;
        } else {
            spacingLeftId = spacingLeftMobile ? spacingLeftMobile : spacingLeft;
            spacingLeftPlaceholder = getValue( spacingLeftMobile, isLeft ) ? spacingLeftMobile : spacingLeft;
        }

    }

    const onChangeSpacing = ( event, spacingId, isExist ) => {

        if ( ! isExist ) {
            return;
        }

        let newValue = event.target.value;

        // Replace negative value except for margin type.
        if ( 'margin' !== isType ) {
            newValue = newValue.toString().replace( /-/g, '' );
        }

        if ( spacingLock === true ) {
            setSpacingAll( newValue );
        } else {
            control.settings[spacingId].set(newValue);
        }
    };

    const onBlurSpacing = (e, spacingId, isExist) => {

        if ( ! isExist ) {
            return;
        }

        const value = e.target.value;

        if (spacingLock === true) {
          setSpacingAll(value);
        } else {
          control.settings[spacingId].set(value);
        }
    };

    const setSpacingAll = ( value ) => {

		if ( isType !== 'margin' ) {
			value = value.toString().replace( /-/g, '' );
		}

		if ( 'desktop' === deviceType ) {
            if ( isTop ) {
                control.settings[spacingTop].set(value);
            }
            if(isRight) {
                control.settings[spacingRight].set(value);
            }
            if(isBottom) {
                control.settings[spacingBottom].set(value);
            }
            if(isLeft) {
                control.settings[spacingLeft].set(value);
            }
		} else if ( 'tablet' === deviceType ) {
            if ( isTop ) {
                control.settings[spacingTopTablet].set(value);
            }
            if(isRight) {
                control.settings[spacingRightTablet].set(value);
            }
            if(isBottom) {
                control.settings[spacingBottomTablet].set(value);
            }
            if(isLeft) {
                control.settings[spacingLeftTablet].set(value);
            }
		} else if ( 'mobile' === deviceType ) {
            if ( isTop ) {
                control.settings[spacingTopMobile].set(value);
            }
            if(isRight) {
                control.settings[spacingRightMobile].set(value);
            }
            if(isBottom) {
                control.settings[spacingBottomMobile].set(value);
            }
            if(isLeft) {
                control.settings[spacingLeftMobile].set(value);
            }
		}

	}

    const onClickSync = () => {

        toggleSpacingLock();

		const spacingDesktop = [
			isTop ? getValue( spacingTop, isTop ) : '0',
			isRight ? getValue( spacingRight, isRight ) : '0',
			isBottom ? getValue( spacingBottom, isBottom ) : '0',
			isLeft ? getValue( spacingLeft, isLeft ) : '0'
		];

		const spacingTablet = [
            isTop ? getValue( spacingTopTablet, isTop ) : '0',
			isRight ? getValue( spacingRightTablet, isRight ) : '0',
			isBottom ? getValue( spacingBottomTablet, isBottom ) : '0',
			isLeft ? getValue( spacingLeftTablet, isLeft ) : '0'
		];

		const spacingMobile = [
            isTop ? getValue( spacingTopMobile, isTop ) : '0',
			isRight ? getValue( spacingRightMobile, isRight ) : '0',
			isBottom ? getValue( spacingBottomMobile, isBottom ) : '0',
			isLeft ? getValue( spacingLeftMobile, isLeft ) : '0'
		];

		if ( 'desktop' === deviceType ) {

            const newValue = Math.max.apply( null, spacingDesktop );

            if ( isTop ) {
                control.settings[spacingTop].set(newValue.toString());
            }
            if(isRight) {
                control.settings[spacingRight].set(newValue.toString());
            }
            if(isBottom) {
                control.settings[spacingBottom].set(newValue.toString());
            }
            if(isLeft) {
                control.settings[spacingLeft].set(newValue.toString());
            }

		} else if ( 'tablet' === deviceType ) {

            const newValue = Math.max.apply( null, spacingTablet );

            if ( isTop ) {
                control.settings[spacingTopTablet].set(newValue.toString());
            }
            if(isRight) {
                control.settings[spacingRightTablet].set(newValue.toString());
            }
            if(isBottom) {
                control.settings[spacingBottomTablet].set(newValue.toString());
            }
            if(isLeft) {
                control.settings[spacingLeftTablet].set(newValue.toString());
            }
		} else if ( 'mobile' === deviceType ) {

            const newValue = Math.max.apply( null, spacingMobile );

            if ( isTop ) {
                control.settings[spacingTopMobile].set(newValue.toString());
            }
            if(isRight) {
                control.settings[spacingRightMobile].set(newValue.toString());
            }
            if(isBottom) {
                control.settings[spacingBottomMobile].set(newValue.toString());
            }
            if(isLeft) {
                control.settings[spacingLeftMobile].set(newValue.toString());
            }
		}

	}

    return (
        <div className='ocean-customize-component-spacing'>

            <div className='spacing__container'>
                <div className='spacing-item'>
                    <input
                        className="spacing-input"
                        type="number"
                        onChange={ (e) => onChangeSpacing( e, spacingTopId, isTop ) }
                        onBlur={ (e) => onBlurSpacing(e, spacingTopId, isTop) }
                        onClick={ ( e ) => {
                            e.currentTarget.focus();
                        } }
                        /* translators: Spacing aria-label */
                        aria-label={ sprintf( __( '%s top', 'oceanwp' ), isType ) }
                        value={ getValue( spacingTopId, isTop ) }
                        placeholder={ getValue( spacingTopPlaceholder, isTop ) }
                        min={ isType === 'padding' ? 0 : undefined }
                        data-attribute={ isType }
                        readOnly={ ! isTop ? 'readonly' : undefined}
                    />
                    <span className='spacing-item-label'>{ __( 'Top', 'oceanwp' ) }</span>
                </div>

                <div className='spacing-item'>
                    <input
                        className="spacing-input"
                        type="number"
                        onChange={ (e) => onChangeSpacing( e, spacingRightId, isRight ) }
                        onBlur={(e) => onBlurSpacing(e, spacingRightId, isRight)}
                        onClick={ ( e ) => {
                            e.currentTarget.focus();
                        } }
                        /* translators: Spacing aria-label */
                        aria-label={ sprintf( __( '%s right', 'oceanwp' ), isType ) }
                        value={ getValue( spacingRightId, isRight ) }
                        placeholder={ getValue( spacingRightPlaceholder, isRight ) }
                        min={ isType === 'padding' ? 0 : undefined }
                        data-attribute={ isType }
                        readOnly={ ! isRight ? 'readonly' : undefined}
                    />
                    <span className='spacing-item-label'>{ __( 'Right', 'oceanwp' ) }</span>
                </div>

                <div className='spacing-item'>
                    <input
                        className="spacing-input"
                        type="number"
                        onChange={ (e) => onChangeSpacing( e, spacingBottomId, isBottom ) }
                        onBlur={(e) => onBlurSpacing(e, spacingBottomId, isBottom)}
                        onClick={ ( e ) => {
                            e.currentTarget.focus();
                        } }
                        /* translators: Spacing aria-label */
                        aria-label={ sprintf( __( '%s bottom', 'oceanwp' ), isType ) }
                        value={ getValue( spacingBottomId, isBottom ) }
                        placeholder={ getValue( spacingBottomPlaceholder, isBottom ) }
                        min={ isType === 'padding' ? 0 : undefined }
                        data-attribute={ isType }
                        readOnly={ ! isBottom ? 'readonly' : undefined}
                    />
                    <span className='spacing-item-label'>{ __( 'Bottom', 'oceanwp' ) }</span>
                </div>

                <div className='spacing-item'>
                    <input
                        className="spacing-input"
                        type="number"
                        onChange={ (e) => onChangeSpacing( e, spacingLeftId, isLeft ) }
                        onBlur={(e) => onBlurSpacing(e, spacingLeftId, isLeft)}
                        onClick={ ( e ) => {
                            e.currentTarget.focus();
                        } }
                        /* translators: Spacing aria-label */
                        aria-label={ sprintf( __( '%s left', 'oceanwp' ), isType ) }
                        value={ getValue( spacingLeftId, isLeft ) }
                        placeholder={ getValue( spacingLeftPlaceholder, isLeft )  }
                        min={ isType === 'padding' ? 0 : undefined }
                        data-attribute={ isType }
                        readOnly={ ! isLeft ? 'readonly' : undefined}
                    />
                    <span className='spacing-item-label'>{ __( 'Left', 'oceanwp' ) }</span>
                </div>

                <div className='spacing-item'>
                    <Tooltip
                        text={ ( spacingLock === true ) ? __( 'Unsync', 'oceanwp' ) : __( 'Sync', 'oceanwp' ) }
                        placement="top"
                    >
                        <Button
                            className="spacing__locked"
                            aria-label={ sprintf( __( 'Sync %s', 'oceanwp' ), label ) }
                            size='small'
                            variant={ spacingLock === true ? 'primary' : 'secondary' }
                            aria-pressed={ spacingLock === true }
                            onClick={ onClickSync }
                        >
                            { ( spacingLock === true ) ? <Icon icon={lock}/> : <Icon icon={unlock}/> }
                        </Button>
                    </Tooltip>
                    <span className='spacing-item-label'>{ __( 'Sync', 'oceanwp' ) }</span>
                </div>
            </div>

        </div>
    );
};

export default OceanSpacing;
