import { __ } from '@wordpress/i18n';
import { BaseControl } from '@wordpress/components';
import Select from 'react-select';
import './style.scss';

const ControlSelect = (props) => {
    const {
        label,
        description,
        hideLabel,
        control,
        choices = {},
    } = props;

    let getValue = control.setting.get();

    if (typeof getValue === 'string') {
        try {
            getValue = JSON.parse(getValue);
        } catch (e) {
            console.error('Failed to parse JSON string:', e);
            getValue = [];
        }
    }

    const showLabel = !hideLabel || 'undefined' === typeof hideLabel;

    const options = Object.keys(choices).map((key) => ({
        value: key,
        label: choices[key],
    }));

    const storedValue = getValue.map((value) => ({
        value: value,
        label: choices[value],
    }));

    const setHandle = (selectedOptions) => {
        const selectedValues = selectedOptions.map((option) => option.value);
        const jsonString = JSON.stringify(selectedValues);
        control.setting.set(jsonString);
    };

    return (
        <BaseControl className="ocean-customize-control__select">
            {!!label && showLabel && (
                <div className="control__select--header">
                    <div className="control__select--label">
                        <span className='customize-control-title'>{label}</span>
                    </div>
                </div>
            )}

            {description && (
                <span
                    className="description customize-control-description ocean-customize-control--description"
                    dangerouslySetInnerHTML={{ __html: description }}
                />
            )}

            <div className='control__select--content'>
                <Select
                    options={options}
                    value={storedValue}
                    onChange={setHandle}
                    isMulti={true}
                    maxMenuHeight={300}
                    className="react-select-container"
                    classNamePrefix="react-select"
                    menuPlacement="auto"
                    menuPortalTarget={document.body}
                    styles={{ menuPortal: base => ({ ...base, zIndex: 9999999 }) }}
                />
            </div>
        </BaseControl>
    );
};

export default ControlSelect;
