import { __ } from '@wordpress/i18n';
import { BaseControl } from '@wordpress/components';
import { useState } from '@wordpress/element';
import './style.scss';

const ControlDropdownPages = (props) => {
    const {
        label,
        description,
        hideLabel,
        control,
    } = props;

    const showLabel = !hideLabel || typeof hideLabel === 'undefined';

    const [value, setValue] = useState(control.setting.get());

    const parseHTML = (html) => {
        const doc = new DOMParser().parseFromString(html, 'text/html');
        const selectElement = doc.querySelector('select');
        console.log('Parsed HTML:', doc.documentElement.outerHTML);
        if (!selectElement) {
            console.error('Select element not found in parsed HTML:', doc.documentElement.outerHTML);
            return [];
        }
        const options = Array.from(selectElement.options).map(option => ({
            value: option.value,
            label: option.text,
        }));
        return options;
    };

    const options = parseHTML(oceanCustomize.pageChoices);

    const handleChange = (event) => {
        const selectedValue = event.target.value;
        setValue(selectedValue);
        control.setting.set(selectedValue);
    };

    return (
        <BaseControl className="ocean-customize-control__select">
            {label && showLabel && (
                <div className="control__select--header">
                    <div className="control__select--label">
                        <span className='customize-control-title'>{label}</span>
                    </div>
                </div>
            )}

            {description && (
                <span
                    className="description customize-control-description ocean-customize-control--description"
                    dangerouslySetInnerHTML={{ __html: description }}
                />
            )}

            <div className='control__select--content'>
                <select value={value} onChange={handleChange}>
                    {options.map(option => (
                        <option key={option.value} value={option.value}>
                            {option.label}
                        </option>
                    ))}
                </select>
            </div>
        </BaseControl>
    );
};

export default ControlDropdownPages;
