import { __ } from '@wordpress/i18n';
import {
    BaseControl,
    Button,
    TextControl,
    SelectControl,
} from '@wordpress/components';
import { useState, useEffect } from '@wordpress/element';

// Style.
import './style.scss';

function ControlSocialLink(props) {
    const {
        label,
        description,
        hideLabel,
        customizerSettings,
        control,
        settingGroup,
    } = props;

    const showLabel = ! hideLabel || 'undefined' === typeof hideLabel;

    const settingsArray = Object.values(settingGroup);

    return(
        <>
            <BaseControl
                className="ocean-customize-control__social_links"
            >
                <div className='control__social_links--content'>
                    { settingsArray.map( (profile, index) => {
                        return(
                            <TextControl
                                key={index}
                                value={ customizerSettings[profile.id].get() }
                                label={ profile.label }
                                onChange={ ( url ) => {
                                    control.settings[profile.id].set( url );
                                } }
                            />
                        )})
                    }
                </div>
			</BaseControl>
        </>
    );
}

export default ControlSocialLink;