import {
	render,
} from '@wordpress/element';

import {
	SlotFillProvider,
	Popover,
} from '@wordpress/components';

const OceanControlForm = {
	extend: ( FormComponent, controlExtend = {} ) => wp.customize.owpControl.extend( Object.assign( {}, {
		/**
		 * After control has been first rendered, start re-rendering when setting changes.
		 *
		 * React is able to be used here instead of the wp.customize.Element abstraction.
		 *
		 * @return {void}
		 */
		ready: function ready() {
			const control = this;

			// Re-render control when setting changes.
			for (const settingKey in control.settings) {
                if (control.settings.hasOwnProperty(settingKey)) {
                  const setting = control.settings[settingKey];
                  setting.bind(() => {
                    control.renderContent();
                  });
                }
            }
		},

		/**
		 * Render the control into the DOM.
		 *
		 * This is called from the Control#embed() method in the parent class.
		 *
		 * @return {void}
		 */
		renderContent: function renderContent() {
			const control = this;

			const form = <SlotFillProvider>
				<FormComponent
					{ ...control.params }
					customizerSetting={ control.setting }
                    customizerSettings={ control.settings }
					control={ control }
				/>
				<Popover.Slot />
			</SlotFillProvider>;

			const wrapper = control.getWrapper();

			render(
				form,
				wrapper
			);
		},
	}, controlExtend ) ),
};

export default OceanControlForm;
