import { __ } from '@wordpress/i18n';
import { BaseControl } from '@wordpress/components';
import { useState } from '@wordpress/element';
import OceanColorPickerButton from '../../components/color-picker/button';
import OceanColorPicker from '../../components/color-picker';

// Style.
import './style.scss';

const ControlColorPicker = ( props ) => {

    const {
        label,
        description,
        hideLabel,
        customizerSettings,
        control,
        settingGroup,
        settingClass,
        defaultValue
    } = props;

	const showLabel = ! hideLabel || 'undefined' === typeof hideLabel;

    const settingsArray = Object.values(settingGroup);

    const [openPickerIndex, setOpenPickerIndex] = useState(null);

	return (
		<>

            { description && (
                <span
                    className="description customize-control-description ocean-customize-control--description"
                    dangerouslySetInnerHTML={ { __html: description } }
                >
                </span>
            )}

			<BaseControl
				className="ocean-customize-control__color-picker"
			>
				{ !! label && showLabel &&
					<div className={`control__color-picker--label ${settingClass ? settingClass : ''}`}>
						<span className='customize-control-title'>{ label }</span>
					</div>
				}

                <div className='control__color-picker--button-wrap'>
                    { settingsArray.map( (element, index) => {
                        return(
                            <OceanColorPickerButton
                                value={ customizerSettings[element.id].get() }
                                tooltip={ element.label || __( 'Choose Color', 'oceanwp' ) }
                                tooltipPosition={ 'top' }
                                modalDisplay={openPickerIndex === index}
                                onToggleModal={() => {
                                    setOpenPickerIndex(openPickerIndex === index ? null : index); // Toggle the index
                                }}
                            />
                        )})
                    }
                </div>
			</BaseControl>

            <div className='control__color-picker--modal'>
                { settingsArray.map( (element, index) => {
                    return(
                        <OceanColorPicker
                            value={ customizerSettings[element.id].get() }
                            showPalette={ true }
                            onChange={ ( color ) => {
                                control.settings[element.id].set( color );
                            } }
                            onClickReset={ () => {
                                control.settings[element.id].set( defaultValue );
                            } }
                            modalDisplay={openPickerIndex === index}
                            onToggleModal={() => {
                                setOpenPickerIndex(openPickerIndex === index ? null : index); // Toggle the index
                            }}
                        />
                    )})
                }
            </div>
		</>
	);
};

export default ControlColorPicker;