import { __ } from '@wordpress/i18n';
import {
    BaseControl
} from '@wordpress/components';
import { decodeEntities } from '@wordpress/html-entities';

// Style
import './style.scss';

function ControlUpsell( props ) {

    const {
        label,
        settingClass,
        isContent,
        hideLabel
    } = props

    const showLabel = ! hideLabel || 'undefined' === typeof hideLabel;

    return(
        <>
            <BaseControl
                className="ocean-customize-control__upsell"
            >
                <div className={`control__upsell-wrap ${settingClass ? settingClass : ''}`}>
                    { !! label && showLabel &&
                        <div className="control__upsell--header">
                            <div className="control__upsell--label">
                                <span className='customize-control-title'>{ label }</span>
                            </div>
                        </div>
                    }

                    { console.log(isContent) }

                    { !! isContent && (
                        <div className="control__upsell-content">
                            { isContent && (
                                <div className="upsell-content--area">
                                    <div
                                        className='is-content'
                                        dangerouslySetInnerHTML={{ __html: isContent }}
                                    ></div>
                                </div>
                            )}
                        </div>
                    )}
                </div>
            </BaseControl>
        </>
    );

}

export default ControlUpsell;