import { __ } from '@wordpress/i18n';
import { useDispatch } from '@wordpress/data';
import {
	ButtonGroup,
	Button,
	Dashicon,
	Tooltip
} from '@wordpress/components'
import { Icon, rotateLeft } from '@wordpress/icons';

const OceanTypoReset = ( props ) => {

    const {
        control,
        fontFamily,
        fontSubset,
        fontSize,
        fontSizeTablet,
        fontSizeMobile,
        fontSizeUnit,
        fontWeight,
        fontWeightTablet,
        fontWeightMobile,
        lineHeight,
        lineHeightTablet,
        lineHeightMobile,
        lineHeightUnit,
        letterSpacing,
        letterSpacingTablet,
        letterSpacingMobile,
        letterSpacingUnit,
        textTransform,
        textTransformTablet,
        textTransformMobile,
        textDecoration,
    } = props;

    return(
            <Tooltip
                text={ __( 'Reset typography settings', 'ocean-extra' ) }
                placement="top"
            >
                <Button
                    className="components-circular-button-icon reset-typo add-buttons"
                    onClick={ () => {
                        control.settings[fontFamily].set( '' );
                        control.settings[fontSize].set( 0 );
                        control.settings[fontSizeMobile].set( 0 );
                        control.settings[fontSizeTablet].set( 0 );
                        control.settings[fontSizeUnit].set( '' );
                        control.settings[fontSubset].set( '' );
                        control.settings[fontWeight].set( 0 );
                        control.settings[fontWeightTablet].set( 0 );
                        control.settings[fontWeightMobile].set( 0 );
                        control.settings[letterSpacing].set( 0 );
                        control.settings[letterSpacingMobile].set( 0 );
                        control.settings[letterSpacingTablet].set( 0 );
                        control.settings[letterSpacingUnit].set( '' );
                        control.settings[lineHeight].set( 0 );
                        control.settings[lineHeightMobile].set( 0 );
                        control.settings[lineHeightTablet].set( 0 );
                        control.settings[lineHeightUnit].set( '' );
                        control.settings[textTransform].set( '' );
                        control.settings[textTransformMobile].set( '' );
                        control.settings[textTransformTablet].set( '' );
                        control.settings[textDecoration].set( '' );
                    } }
                    aria-label={ __( 'Reset', 'ocean-extra' ) }
                >
                    { <Icon icon={ rotateLeft } /> }
                </Button>
            </Tooltip>
    )

}

export default OceanTypoReset;