import { __ } from '@wordpress/i18n';
import {
	BaseControl,
	Tooltip,
	ColorPicker,
	Button,
} from '@wordpress/components';
import {
	ColorPalette
} from '@wordpress/block-editor';
import { Icon, rotateLeft, close } from '@wordpress/icons';

// Style.
import './style.scss';

const OceanColorPicker = ( props ) => {

    const {
        value,
        tooltip,
		tooltipPosition,
        showPalette,
        onChange,
        onClickReset,
        modalDisplay,
        modalStyle = 'style-popup',
        onToggleModal,
    } = props;

    const predefinedColors = [
        { name: 'hot-pink', color: '#FB6B90' },
        { name: 'fuchsia', color: '#FB4570' },
        { name: 'teal', color: '#29A0B1' },
        { name: 'spearmint', color: '#98D7C2' },
        { name: 'yellow', color: '#FAD02C' },
        { name: 'misty-blue', color: '#2F5061' },
        { name: 'white', color: '#EFEBE0' }
    ];

    let toggleButton = (
        <div className="color-picker__header--button">
            <div className='components-color-palette__item-wrapper components-circular-option-picker__option-wrapper'>
                <Tooltip text={ tooltip } placement={ tooltipPosition }>
                    <button
                        type="button"
                        className="components-color-palette__item components-circular-option-picker__option"
                        onClick={onToggleModal}
                        aria-expanded={ modalDisplay }
                        aria-label={ __( 'Color picker', 'oceanwp' ) }
                        style={ { color: value ? value : 'transparent' } }
                    >
                        <span className="components-color-palette__custom-color-gradient" />
                    </button>
                </Tooltip>
            </div>
        </div>
    );

    return(
        <BaseControl className="ocean-customize-component-color-picker">

            <div className="color-picker__container">

                { 'style-inline' === modalStyle && toggleButton }

                <div className={`color-picker__popup ${ modalDisplay ? 'show-popup' : '' } ${ modalStyle ? modalStyle : '' }`}>
                    <div className='color-picker__popup--area'>

                        { showPalette &&
                            <BaseControl
                                className="color-picker__palette"
                            >
                                <ColorPalette
                                    colors={ predefinedColors }
                                    value={ value }
                                    onChange={ ( color ) => {
                                        if ( 'undefined' === typeof color ) {
                                            color = '';
                                        }

                                        onChange( color );
                                    } }
                                    disableCustomColors={ true }
                                    clearable={ false }
                                />

                                <Tooltip
                                    text={ __( 'Close', 'ocean-extra' ) }
                                    placement="top"
                                >
                                    <Button
                                        className="components-circular-button-icon close-color add-buttons"
                                        onClick={ onToggleModal }
                                        aria-label={ __( 'Close', 'oceanwp' ) }
                                    >
                                        { <Icon icon={ close } /> }
                                    </Button>
                                </Tooltip>
                            </BaseControl>
                        }

                        <BaseControl
                            className='color-picker__sketch'
                        >
                            <ColorPicker
                                color={ value }
                                onChangeComplete={ ( color ) => {
                                    if ( 'undefined' === typeof color ) {
                                        color = '';
                                    }
                                    onChange( color.hex );
                                } }
                            />
                        </BaseControl>

                        <div className='color-picker__reset'>
                            <div className='color-picker__reset-wrap'>
                                <div className='color-picker__reset-label'>
                                    <span className='title-text'>{ __( 'Reset Color', 'oceanwp' ) }</span>
                                </div>
                                <div className='color-picker-reset__button'>
                                    <Tooltip text={__( 'Reset Color', 'oceanwp' )} placement="top">
                                        <Button
                                            className="circular-button-icon"
                                            onClick={ onClickReset }
                                            aria-label={ __( 'Reset color', 'oceanwp' ) }
                                        >
                                            { <Icon icon={ rotateLeft } /> }
                                        </Button>
                                    </Tooltip>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </BaseControl>
    );
}

export default OceanColorPicker;