import icons from './icons.json'

/**
 *
 * @param {string} icon    Icon name.
 * @return {string} class  Icon class.
 */
export default function svgIcon( icon, customClass = '' ) {

	const renderSVG = (icon) => (
		<span
			className={`ocean-svg has-current-colors ${ customClass ? customClass : '' }`}
			dangerouslySetInnerHTML={{__html: icon }}
		>
		</span>
	)

	for (const [key, value] of Object.entries(icons)) {
		if( icon === key ) {
			return(
				renderSVG( value )
			);
		}
	}
}