import { __ } from '@wordpress/i18n';
import { BaseControl } from '@wordpress/components';
import { useState, useEffect } from '@wordpress/element';
import OceanTypography from '../../components/typography';
import OceanTypographyToggleButton from '../../components/typography/toggleButton';

// Style.
import './style.scss';

const ControlTypography = ( props ) => {

    const {
        label,
        description,
        hideLabel,
        customizerSettings,
        control,
        settingGroup,
        defaultValue,
        settingClass,
        selector
    } = props;

	const showLabel = ! hideLabel || 'undefined' === typeof hideLabel;

    //const settingsArray = Object.values(settingGroup);

    const [openModal, setOpenModal] = useState(null);

    const modalToggleHandle = () => {
        if ( true === openModal) {
            setOpenModal( false );
        } else {
            setOpenModal( true );
        }
    }

    const [currentView, setCurrentView] = useState(
		wp.customize && wp.customize.previewedDevice
			? wp.customize.previewedDevice()
			: 'desktop'
	)

	const listener = () => {
		setCurrentView(
			wp.customize && wp.customize.previewedDevice
				? wp.customize.previewedDevice()
				: 'desktop'
		)
	}

    useEffect(() => {
		if (!wp.customize) return
		setTimeout(() => wp.customize.previewedDevice.bind(listener), 1000)

		return () => {
			if (!wp.customize) return
			wp.customize.previewedDevice.unbind(listener)
		}
	}, [])

	return (
		<>
            { description && (
                <span
                    className="description customize-control-description ocean-customize-control--description"
                    dangerouslySetInnerHTML={ { __html: description } }
                >
                </span>
            )}

			<BaseControl
				className="ocean-customize-control__typography"
			>
				{ !! label && showLabel &&
					<div className={`control__typography--label ${settingClass ? settingClass : ''}`}>
						<span className='customize-control-title'>{ label }</span>
					</div>
				}

                <div className='control__typography--button-wrap'>
                    <OceanTypographyToggleButton
                        modalDisplay={openModal}
                        onToggleModal={ modalToggleHandle }
                    />
                </div>
			</BaseControl>

            <OceanTypography { ...props }
                deviceType={ currentView }
                fontFamily={ 'fontFamily' }
                fontSubset={ 'fontSubset' }
                fontWeight={ 'fontWeight' }
                fontWeightTablet={ 'fontWeightTablet' }
                fontWeightMobile={ 'fontWeightMobile' }
                fontSize={ 'fontSize' }
                fontSizeTablet={ 'fontSizeTablet' }
                fontSizeMobile={ 'fontSizeMobile' }
                fontSizeUnit={ 'fontSizeUnit' }
                lineHeight={ 'lineHeight' }
                lineHeightTablet={ 'lineHeightTablet' }
                lineHeightMobile={ 'lineHeightMobile' }
                lineHeightUnit={ 'lineHeightUnit' }
                letterSpacing={ 'letterSpacing' }
                letterSpacingTablet={ 'letterSpacingTablet' }
                letterSpacingMobile={ 'letterSpacingMobile' }
                letterSpacingUnit={ 'letterSpacingUnit' }
                textTransform={ 'textTransform' }
                textTransformTablet={ 'textTransformTablet' }
                textTransformMobile={ 'textTransformMobile' }
                textDecoration={ 'textDecoration' }
                textColor={ 'textColor' }
                textColorHover={ 'textColorHover' }
                modalDisplay={openModal}
                onToggleModal={ modalToggleHandle }
            />
		</>
	);
};

export default ControlTypography;