import { __ } from '@wordpress/i18n';
import {
    BaseControl,
    TextareaControl
} from '@wordpress/components';

// Style.
import './style.scss';

const ControlTextArea = ( props ) => {

    const {
        label,
        description,
        hideLabel,
        control,
    } = props;

    let value = control.setting.get();

	const showLabel = ! hideLabel || 'undefined' === typeof hideLabel;

	return (
		<>
			<BaseControl
				className="ocean-customize-control__textarea"
			>
				{ !! label && showLabel &&
					<div className="control__textarea--header">
                        <div className="control__textarea--label">
                            <span className='customize-control-title'>{ label }</span>
                        </div>
                    </div>
				}

                { description && (
                    <span
                        className="description customize-control-description ocean-customize-control--description"
                        dangerouslySetInnerHTML={ { __html: description } }
                    >
                    </span>
                )}

                <div className='control__textarea--content'>
                    <TextareaControl
                        value={value}
                        rows='6'
                        onChange={ ( text ) => control.setting.set(text) }
                    />
                </div>
			</BaseControl>
		</>
	);
};

export default ControlTextArea;