import { __ } from '@wordpress/i18n';
import {
	ButtonGroup,
	Button,
	Dashicon,
	Tooltip
} from '@wordpress/components'
import {
	useState,
	useEffect,
} from '@wordpress/element'

// Style.
import './style.scss';

const OceanDeviceType = () => {

    const [currentView, setCurrentView] = useState(
		wp.customize && wp.customize.previewedDevice
			? wp.customize.previewedDevice()
			: 'desktop'
	)

	const listener = () => {
		setCurrentView(
			wp.customize && wp.customize.previewedDevice
				? wp.customize.previewedDevice()
				: 'desktop'
		)
	}

    useEffect(() => {
		if (!wp.customize) return
		setTimeout(() => wp.customize.previewedDevice.bind(listener), 1000)

		return () => {
			if (!wp.customize) return
			wp.customize.previewedDevice.unbind(listener)
		}
	}, [])

    const devices = [
        {
            name: 'Desktop',
            key: 'desktop',
            title: <Dashicon icon="desktop" />,
            itemClass: 'ocean-device-preview preview-desktop',
        },
        {
            name: 'Tablet',
            key: 'tablet',
            title: <Dashicon icon="tablet" />,
            itemClass: 'ocean-device-preview preview-tablet',
        },
        {
            name: 'Mobile',
            key: 'mobile',
            title: <Dashicon icon="smartphone" />,
            itemClass: 'ocean-device-preview preview-mobile',
        },
    ];

    return(
        <ButtonGroup className="ocean-customize-component-device-preview" aria-label={ __( 'Device', 'oceanwp' ) }>
            { devices.map( ( device ) => {
                return(
                    <Tooltip
                        key={ device.name }
                        /* translators: Unit type */
                        text={ sprintf( __( '%s', 'oceanwp' ), device.name ) }
                        placement="top"
                    >
                        <Button
                            key={ device.key }
                            className={ `components-button components-tab-panel__tabs-item ${ device.itemClass }${ device.key === currentView ? ' active-tab' : '' }` }
                            aria-pressed={ currentView === device.key }
                            onClick={ () => {
                                setCurrentView(device.key)
                                wp.customize && wp.customize.previewedDevice.set( device.key )
                            }}
                        >
                            { device.title }
                        </Button>
                    </Tooltip>
                )
            } ) }
        </ButtonGroup>
    )

}

export default OceanDeviceType;