import { useState, useEffect } from '@wordpress/element';
import { Icon } from '@wordpress/icons';

// Style.
import './style.scss';

const AccordionItem = ({ icon, title, content, isOpen, toggleItem }) => {

	return (
		<div className={`accordion__item ${ isOpen ? 'accordion-open' : '' }`}>

			<div className="accordion__header" onClick={toggleItem}>
				{icon && <Icon icon={icon} className="accordion__icon" />}
				<h3 className="accordion__title">{title}</h3>
			</div>

			{ isOpen && (
				<div className="accordion__content">
					<div className='accordion__description'>{content}</div>
				</div>
			)}

		</div>
	);
};

const OceanAccordion = ( props ) => {
	const accordionItems = props.AccordionArray
	const [openItemIndex, setOpenItemIndex] = useState(props.initialStatus ? 0 : null);

	const toggleItem = (index) => {
		setOpenItemIndex((prevIndex) => (prevIndex === index ? null : index));
	};

	return (
		<div className='ocean-accordion-component'>
			<div className='accordion__container'>
				{ accordionItems.map((item, index) => (
					<AccordionItem
						key={index}
						icon={item.icon}
						title={item.title}
						content={item.content}
						isOpen={ !! openItemIndex ? true : openItemIndex === index }
						toggleItem={() => toggleItem(index)}
					/>
				))}
			</div>
		</div>
	);
};

export default OceanAccordion;
